import { Component, Input, Output, EventEmitter } from "@angular/core";

@Component({
  selector: 'lib-url',
  template: `<input
      type="url"
      [ngClass]="{
        'form-control': true
      }"
      [(placeholder)]="placeholder"
      [(ngModel)]="value"
      [(name)]="name"
      [(id)]="id"
      #url="ngModel"
      [(disabled)]="disabled"
      [(tabindex)]="tabindex"
      (focus)="onFocus($event)"
      (blur)="onBlur($event)"
      (input)="onChange($event)"
      [pattern]="onValidateUrl(urltype, regex)"
      autofocus
      required
    />

    <div
      class="md-errors-spacer"
      *ngIf="url.invalid && url.touched"
    >
      <div *ngIf="url.errors && url.errors.pattern">
        <p class="error-text">{{ urlErrorName }}</p>
      </div>
    </div>`,
  styles: [`.form-control{width:50%;padding:12px 20px;margin:8px 0;display:inline-block;border:1px solid #ccc;box-sizing:border-box}.error-text{color:red}`]
})
export class UrlComponent {

  @Input() urlErrorName: string =
  "The url entered is not valid and must be changed";
@Input() urltype: String;
@Input() disabled: boolean = false;
@Input() value: String;
@Input() tabindex: Number = 1;
@Input() id: String = "facebookUrl";
@Input() name: String = "facebookUrl";
@Input() placeholder: String = "Enter Url";
@Input()
regex: RegExp = /(?:(?:http|https):\/\/)?(?:www.)?facebook.com\/(?:(?:\w)*#!\/)?(?:pages\/)?(?:[?\w\-]*\/)?(?:profile.php\?id=(?=\d.*))?([\w\-]*)?/;

@Output() change: EventEmitter<any> = new EventEmitter<any>();
@Output() focus: EventEmitter<any> = new EventEmitter<any>();

@Output() blur: EventEmitter<any> = new EventEmitter<any>();

onFocus(event) {
  this.focus.emit(event);
}
onBlur(event) {
  this.blur.emit(event);
}
onChange(event) {
  this.change.emit(event);
}

onValidateUrl(type, regex) {
  debugger;
  let urlRegex;
  switch (type) {
    case "facebook":
      urlRegex = /(?:(?:http|https):\/\/)?(?:www.)?facebook.com\/(?:(?:\w)*#!\/)?(?:pages\/)?(?:[?\w\-]*\/)?(?:profile.php\?id=(?=\d.*))?([\w\-]*)?/;
      break;
    case "instagram":
      urlRegex = /(?:(?:http|https):\/\/)?(?:www\.)?instagram.com(\w+:{0,1}\w*@)?(\S+)(:([0-9])+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;
      break;
    case "linkedin":
      urlRegex = /(?:(?:http|https):\/\/)?(?:www\.)?linkedin.com(\w+:{0,1}\w*@)?(\S+)(:([0-9])+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;
      break;
    case "web":
      urlRegex = /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)?[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/;
      break;
    case "other":
      urlRegex = regex;
      break;
    default:
      urlRegex = regex;
      break;
  }
  return urlRegex;
}
}
